.s-top {
  background-color: #fff;
  &__inner {
    display: flex;
    align-items: flex-end;
    padding: 20px 0 18px;
  }
}
.s-top-logo {
  align-self: flex-start;
  width: 130px;
  a {display: inline-block;}
  img {
    display: block;
    width: 85px;
    height: 37px;
  }
}
.s-top-menu {
  display: flex;
  flex-grow: 1;
  li {
    display: inline-block;
    min-width: 195px;
    text-align: center;
  }
  a {
    display: inline-block;
    font-size: 15px;
    line-height: 34px;
    font-weight: 600;
    font-family: $segoe;
    text-transform: uppercase;
    white-space: nowrap;
    padding: 0 15px;
  }
}
.s-top-btn {
  margin-left: auto;
  .btn {}
}

.menu-btn {
  margin: 0;
  cursor: pointer;
  height: 45px;
  &::before {
    content: '';
    display: block;
    width: 30px;
    margin: 15px 15px 0 15px;
    box-shadow: 0 0 0 1px $c-green, 0 8px 0 1px $c-green, 0 16px 0 1px $c-green;
  }
}


@media (max-width: $lg-max) {
  .s-top {
    position: relative;
    z-index: 1;
    &__inner {align-items: center;}
  }
  .s-top-logo {display: none;}
  .s-top-menu {
    display: block;
    position: absolute;
    top: 100%; left: 0; right: 0;
    background-color: #fff;
    border-bottom: 1px solid $c-green;
    padding-bottom: 20px;
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
    transition: .2s;
    li, a {display: block;}
  }
  .menu-toggle:not(:checked) ~ .s-top-menu {visibility: hidden; opacity: 0;transform: translateY(-20px);}
}