.footer {
  background-color: #ede9e9;
  padding: 45px 0 0;
}

.footer-social {
  .icon {margin-right: 20px; transition: opacity .2s;}
  .icon:hover {opacity: .7;}
}

.footer-nav {margin-top: 40px;}
.footer-col {
  ul {padding-bottom: 20px;}
  li {line-height: 20px; padding: 10px 0;}
  b {font-family: $segoe; font-weight: 600; font-size: 15px; text-transform: uppercase;}
  a {font-size: 14px; color: #575757;}
  a:hover {color: $c-green;}
}

.s-bottom {
  padding: 25px 0 0;
  font-size: 14px;
  line-height: 16px;
  [class^=col] {padding-bottom: 25px;}
  .btn {text-transform: uppercase;}
}


