html {
  height: 100%;
  font-size: 16px;
}

body {
  position: relative;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  overflow-y: auto;
  font-weight: 400;
  font-family: Arial, sans-serif;
  line-height: 1.25;
  color: $c-dark;
  background-color: #fff;
}

h1, .h1 {
}

h2, .h2 {
  font-family: $segoe;
  font-weight: 700;
  font-size: 55px;
  line-height: 60px;
  text-align: center;
  margin-bottom: 20px;
}

p {
  margin-bottom: 15px;
}

.p-light {
  font-family: $segoe;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: ol;
}

.t-light {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}

.s-text {
  padding: 50px 0;
}

img {max-width: 100%;}


@media (min-width: $lg-min) {
  .mobile-only {display: none!important;}
}

@media (max-width: $lg-max) {
  .desktop-only {display: none!important;}
  h2, .h2 {
    font-size: 35px;
    line-height: 40px;
  }
}