.s-text-get-1 {
  padding-bottom: 0;
  h2 {color: $c-green; margin-top: 35px;}
  h2 + p {max-width: 620px;}
  a {font-weight: 700;}
}

.s-text-get-1-list {
  background-color: #f2f0ef;
  padding-bottom: 40px;
  ul {justify-content: space-between;}
  li {
    padding: 35px 15px 0;
    position: relative;
    text-align: center;
    &::before {
      content: '';
      width: 60px; height: 60px;
      display: block;
      margin: 0 auto 20px;
      border: 2px solid $c-green;
      border-radius: 50%;
    }
    &::after {
      content: '';
      position: absolute;
      top: 50px; left: 0; right: 0;
      margin: auto;
      border-width: 0 0 8px 8px;
      border-color: $c-green;
      border-style: solid;
      height: 20px;
      width: 30px;
      transform: rotate(-45deg);
    }
    &:empty {
      &::before, &::after {display: none;}
    }
    strong {color: $c-green;}
  }
}

.s-text-get-2 {
  .p-light {max-width: 750px;}
  ol { padding-bottom: 15px; }
  ol + p {color: $c-green; max-width: 710px; margin: auto; text-align: center;}
  li {
    margin-bottom: 35px;
    max-width: 350px;
    text-align: center;
    counter-increment: ol;
    &::before {
      content: counter(ol);
      display: block;
      font-family: $segoe;
      font-size: 40px;
      font-weight: 700;
      text-align: center;
      width: 56px;
      height: 56px;
      line-height: 56px;
      color: #fff;
      background-color: $c-green;
      border-radius: 50%;
      margin: 0 auto 10px;
    }
  }
}

.s-text-get-3 {
  background-color: #ddf6e7;
  .p-light {max-width: 750px; margin-bottom: 35px;}
  ul {max-width: 750px; margin: auto;}
  li {
    margin-bottom: 40px;
    text-align: center;
    img {
      display: block;
      margin: 0 auto 10px;
    }
    &:last-child {margin-bottom: 0;}
  }
}

.s-text-get-4 {
  padding: 30px 0;
  p {max-width: 780px; margin: auto; font-size: 18px; line-height: 24px; text-align: center;}
}


@media (min-width: $lg-min) {
  .s-text-get-1-list {
    li {
      flex: 1 1 33.3333%;
      &:nth-child(3n-2), &:nth-child(3n-3) {max-width: 320px;}
      &:nth-child(3n-1) {max-width: 375px;}
      &:empty {}
    }
  }
}

@media (max-width: $lg-max) {
  .s-text-get-1-list {
    ul {justify-content: center;}
    li {
      flex: none;
      width: 50%;
      max-width: 100%;
      &:empty {display: none;}
    }
  }

  .s-text-get-2 li {max-width: 100%;}
}

@media (max-width: $sm-max) {
  .s-text-get-1-list li {width: 100%;}
}