.s-calc {
  padding: 63px 0;
  background: #d0cece url('../img/bg-calc.jpg') center / cover no-repeat;
}

.calc {
  max-width: 520px;
  margin-left: auto;
  &__inner {
    max-width: 453px;
    border: 1px solid #808180;
    background-color: #fff;
    padding: 30px;
  }
  &__group {
    position: relative;
    & + & {margin-top: 20px;}
  }
  &__label {
    color: #97aab7;
    font-size: 24px;
    line-height: 1;
    font-family: $segoe;
    font-weight: 300;
    margin-bottom: 20px;
  }
  &__control {}
  &__units {
    color: #8d908e;
    position: absolute;
    right: 0; bottom: 0;
    white-space: nowrap;
    font-size: 18px;
    line-height: 30px;
    width: 68px;
    pointer-events: none;
  }

  &__table {
    display: table;
    width: 100%;
    font-size: 18px;
    line-height: 24px;
    margin: 20px 0;
  }
  &__tr {
    display: table-row;
  }
  &__td {
    display: table-cell;
    width: 50%;
    padding: 6px 0;
    vertical-align: middle;
    &:first-child {
      color: #8d908e;
    }
    &:last-child {
      font-weight: 700;
    }
  }

  &__submit {
    .btn {
    }
  }
}

@media (max-width: $lg-max) {
  .calc {
    margin-right: auto;
    &__inner {max-width: 100%;}
  }
}

@media (max-width: $md-max) {
  .calc {
    &__label {font-size: 20px;}
    &__control {font-size: 40px; height: 50px; line-height: 50px;}
    &__units {font-size: 15px;}
    &__table {font-size: 16px;}
  }
}