.s-text-nav {
  background-color: #1f212b;
  padding: 92px 0 45px;
  color: #fff;
  font-family: $segoe;
  font-size: 32px;
  line-height: 36px;
  font-weight: 300;
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  li {
    padding: 0 10px;
    text-align: center;
    margin-bottom: 40px;
  }
  a {
    display: block;
    &::before {
      content: '';
      display: block;
      margin: 0 auto 30px;
      width: 123px;
      height: 123px;
    }
  }
  li:nth-child(1) a::before {content: url('../img/nav-1.png');}
  li:nth-child(2) a::before {content: url('../img/nav-2.png');}
  li:nth-child(3) a::before {content: url('../img/nav-3.png');}
  li:nth-child(4) a::before {content: url('../img/nav-4.png');}
}

.s-text-main-1 {
  h2 {
    color: $c-green;
    & ~ .p-light {max-width: 620px;}
  }
  img {
    display: block;
    margin: 35px auto;
  }
  .p-light {}
  ol {
    margin-top: 15px;
    margin-left: -15px;
    column-count: 2;
    b {
      display: block;
      font-size: 22px;
      font-weight: 700;
      color: $c-green;
      &::before {
        content: counter(ol)'. ';
        counter-increment: ol;
      }
    }
  }
  li {
    display: inline-block;
    width: 100%;
    line-height: 22px;
    padding-left: 15px;
    padding-top: 20px;
    max-width: 510px;
  }
}

.s-text-main-2 {
  background-color: #d6f3e2;
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    justify-content: space-between;
  }
  &__col {
    padding-left: 15px;
    padding-top: 35px;
    max-width: 500px;
    line-height: 20px;
  }
  .p-light {max-width: 730px; margin-bottom: 0;}
  b {display: block; margin-bottom: 20px;}

  ol {
    li {
      display: flex;
      align-items: center;
      counter-increment: ol;
      &::before {
        content: counter(ol);
        flex: none;
        font-family: $segoe;
        font-size: 40px;
        font-weight: 700;
        text-align: center;
        width: 56px;
        height: 56px;
        line-height: 56px;
        color: #fff;
        background-color: $c-btn-green;
        border-radius: 50%;
        margin-right: 15px;
      }
      & + li {margin-top: 1rem;}
    }
    p {margin: 0;}
  }

  ul {
    margin-bottom: 1rem;
    li {
      &::before {
        content: 'â€¢ ';
      }
    }
  }

}

.s-text-main-3 {
  .p-light {max-width: 720px;}
  ol {
    margin-left: -15px;
    margin-top: 30px;
    margin-bottom: 15px;
    display: flex;
    flex-wrap: wrap;
  }
  li {
    counter-increment: ol;
    display: flex;
    width: 50%;
    padding-left: 15px;
    &::before {
      content: counter(ol);
      flex: none;
      font-family: $segoe;
      font-size: 60px;
      line-height: 1;
      font-weight: 700;
      color: $c-green;
      padding-left: 15px;
      padding-right: 20px;
      display: inline-block;
      margin-top: -8px;
    }
    p {max-width: 420px;}
  }
}

@media (max-width: $xl-max) {
  .s-text-main-2 {
    &__col {max-width: 50%;}
  }
}

@media (max-width: $md-max) {
  .s-text-main-1 {
    ol {column-count: 1;}
    li {display: block; max-width: 100%;}
  }

  .s-text-main-2 {
    &__col {max-width: 100%;}
  }

  .s-text-main-3 {
    li {
      width: 100%;
      p {max-width: 100%;}
    }
  }
}