.form-group {
  margin-bottom: 30px;
}

.form-control {
  display: block;
  width: 100%; height: 66px;
  font-size: 4rem;
  line-height: 67px;
  font-weight: 300;
  font-family: $segoe;
  padding: 0 68px 0 10px;
  color: $c-btn-green;
  background-color: #ebfcf2;
  background-clip: padding-box;
  border: 0;
  outline: 0;
  border-radius: 0;
  transition: border-color .15s ease-in-out;
  &::placeholder {
    color: inherit; 
    opacity: 1;
  }
  &[type="number"] {
    -moz-appearance: textfield;
    &::-webkit-inner-spin-button, 
    &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}

.hidden-control {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.btn {
  display: block;
  width: 100%;
  font-family: $segoe;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  color: #fff;
  background-color: $c-btn-green;
  padding: 0 15px;
  font-size: 15px;
  line-height: 45px;
  border-radius: 0;
  box-shadow: none;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  &:not(:disabled):not(.disabled) {
    cursor: pointer;
  }
  &:focus {outline: 0;}
  &:hover {
    text-decoration: none;
    color: $c-btn-green;
    background-color: #fff;
    border-color: currentColor;
  }
}

.btn-calc {
  font-weight: 300;
  font-size: 24px;
  line-height: 66px;
  &:hover {
    color: $c-btn-green;
    background-color: #fff;
    border-color: currentColor;
  }
}

.btn-cab {
  background-color: #d6f3e2;
  color: #000;
  line-height: 34px;
  border: 0;
  text-transform: uppercase;
  padding: 0 10px;
  &:hover {
    color: #fff;
    background-color: $c-btn-green;
  }
}

.btn-message {
  background-color: #fff;
  color: $c-btn-green;
  border-color: currentColor;
  &:hover {
    color: #fff;
    background-color: $c-green;
  }
}

.btn-call {
  &:hover {
    color: #fff;
    background-color: $c-green;
  }
}