.s-text-steps-head {
  padding: 85px 0 100px;
}

.s-text-steps-0 {
  background-color: #f2f0ef;
  [class^="col"] {margin-bottom: 20px;}
  &__img {
    width: 108.11%;
    position: relative;
    &--left {margin-top: -30px;}
    &--right {margin-left: -8.11%;}
  }
  &__text {
    padding: 10px 15px;
    background-color: $c-btn-green;
    color: #fff;
    text-align: center;
    position: relative;
    z-index: 1;
    svg {
      fill: $c-btn-green;
      position: absolute;
    }
    &--right {
      top: 16%;
      svg {
        top: -25px; right: 100%;
        width: 75px; height: 95px;
      }
    }
    &--left {
      top: 22%;
      svg {
        bottom: -40px; left: 100%;
        width: 200px; height: 110px;
      }
    }
  }
}

[class^="s-text-steps-"] h2 {text-align: left; margin-top: -10px;}
[class^="s-text-steps-"] img {display: block;}

.s-text-steps-1 {
  background-color: #d6f3e2;
}

.s-text-steps-2 {
}

.s-text-steps-3 {
  background-color: #d6f3e2;
  ol {
    margin-bottom: 1rem;
    li {
      counter-increment: ol;
      &::before {content: counter(ol)'. ';}
    }
  }
}

@media (max-width: $xl-max) {
  .s-text-steps-0 {
    &__img {
      width: auto;
      &--right {margin-left: 0;}
    }
    &__text {
      &--right {}
      &--left {
        svg {
          width: 190px; height: 60px;
          bottom: 0;
        }
      }
    }
  }
}

@media (max-width: $lg-max) {
  .s-text-steps-0 {
    &__text {
      &--right {margin: 30px 0 0; top: 0;}
      &--left {
        top: 0;
        svg {
          width: 130px; height: 100px;
        }
      }
    }
  }
}

@media (max-width: $md-max) {
  [class^="s-text-steps-"] h2 {margin-top: 1rem;}
  [class^="s-text-steps-"] img {margin: 0 auto;}

  .s-text-steps-0 {
    &__text--right {margin-top: 0;}
    &__text--right svg {
      bottom: 100%; right: 0; top: auto; left: auto;
      width: 50px; height: 150px;
    }
    &__text--left svg {
      top: 100%; left: 0; bottom: auto; right: auto;
      width: 100px; height: 150px;
    }
  }
}

@media (max-width: $sm-max) {
  .s-text-steps-0 {
    &__text {
      &--right {}
      &--left {}
    }
  }
}